@charset "utf-8";

@font-face {
    font-family: "twayfly";
    src: url("./fonts/tway_fly.ttf");
}
body {
    font-family: "twayfly";
    background: linear-gradient(#99ffff,#fff );
}
.wrapbox {
    background: #ffc;
}
* {
    margin: 0;
    padding: 0;
}
.Logobox {
    display: flex;
    justify-content: center;
    align-items: center;
}
.mainLogo {
    width: 250px;
    height: 250px;
}
li {
    text-decoration: none;
    list-style-type: none;
}
a {
    text-decoration: none;
    color: #033F73;
}
a:hover {
    text-decoration: none;
    color: #033F73;
}
a:focus {
    text-decoration: none;
    color: #033F73;
}
a:visited {
    text-decoration: none;
    color: #033F73;
}
.Menu {
    text-align: center;
}
.Menu > li {
    display: inline-block;
    margin-right: 40px;
    font-size: 1.5em;
}
.DomesticBox {
    margin: 50px 30% 0 30%;
}
.doProduct {
    border: 1px solid #ccc;
    margin-bottom: 10px;
}
.DoBox {
    display: inline-block;
    margin-top: 50px;
    width: 630px;
    height: 250px;
}
.DoBox img {
    width: 150px;
    height: 200px;
}
.DoBox > h2 {
    display: inline-block;
    width: 400px;
    vertical-align: top;
    text-align: center;
    padding-left: 20px;
}

.DoBox > a {
    float: right;
    font-size: 1.2em;
}
.DoBox > label {
    cursor: pointer;
    float: right;
    font-size: 1.2em;
    margin-right: 10px;
}
.hidden {
    display: none;
    width: 400px;
    height: 300px;
    text-align: justify;
    margin: 50px 0;
    padding-left: 100px;
}
.DoBox > label {
    color: #033F73;;
}
input#btn1{
    display: none;
}
input#btn1:checked + .hidden {
    display: block;
}
input#btn2{
    display: none;
}
input#btn2:checked + .hidden {
    display: block;
}
input#btn3{
    display: none;
}
input#btn3:checked + .hidden {
    display: block;
}
input#btn4{
    display: none;
}
input#btn4:checked + .hidden {
    display: block;
}
input#btn5{
    display: none;
}
input#btn5:checked + .hidden {
    display: block;
}

